*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  display: flex;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

body {
  max-width: $max-width;
  min-width: $min-width;
  max-height: $max-height;
  min-height: $min-height;
  margin: auto;
}

.App {
  position: relative;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

.main-root {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  align-items: center;
  padding: 0px 20px;
}

.main-app {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-error {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.main {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

a {
  text-decoration: none;
  color: $white;
}

.relative {
  position: relative;
}

.no-display {
  display: none;
}
